
div, ul, li{
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Microsoft YaHei', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #666;
  margin: 10px auto;
  padding: 10px 30px;
}

.catalog{
	width: 100%;
	height: 100%;
	float: left;
}
.catalog .cata-title{
	color: #288bde;
}
.cata-wrap1{
}
.cata-wrap1 ul{
	list-style: none;
}
.cata-wrap1 ul li{
	width: 100px;
	height: 26px;
	text-align: center;
	line-height: 26px;
	border: 2px solid #eee;
	background-color: #eee;
	float: left;
	margin: 10px 10px 0 0;
	cursor: pointer;
	border-radius: 4px;
}
.cata-wrap1 li.active{
	background-color: #fff;
	border: 2px solid #288bde;
}

.list{
	float: left;
	width: 500px;
	height: 600px;
	border: 2px solid #288bde;
	margin: 10px 10px 10px 0;
	border-radius: 4px;
	position: relative;
}
.list .list-caption{
	height: 40px;
	line-height: 40px;
	font-size: 20px;
	color: #fff;
	background-color: #288bde;
	text-align: center;
}
.tb{
	border: none;
	width: 100%;
	cursor: default;
}
.tb td, .tb th{
	text-align: center;
	color: #666;
	font-size: 14px;
}
.tb th{
	font-weight: normal;
	height: 25px;
	line-height: 20px;
	background-color: #eee;
}
.tb tbody tr.tractive{
	background-color: #e9f3fc;
}
.tb td{
	height: 35px;
	line-height: 35px;
	border-bottom: 1px solid #ddd;
	position: relative;
}
.tb td:nth-of-type(1){
	width: 20%;
}
.tb td:nth-of-type(2){
	width: 30%;
	max-width: 90px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.tb td:nth-of-type(3){
	width: 16%;
}
.tb td:nth-of-type(4){
	width: 16%;
}
.tb td:nth-of-type(5){
	width: 16%;
}
.tb .del{
	display: inline-block;
	width: 20px;
	height: 20px;
	line-height: 20px;
	cursor: pointer;
	border-radius: 50%;
}
.tb .del:hover{
	color: #fff;
	background-color: #288bde;
}
.total{
	width: 100%;
	height: 50px;
	position: absolute;
	left: 0;
	bottom: 0;
	text-align: right;
}
.total .total-wrap{
	margin-right: 20px;
}
.total .total-price, .total .total-unit{
	font-weight: bold;
	color: red;
	font-size: 12px;
}
.total .total-wrap .total-price{
	font-size: 24px;
}
.pop-layer{
	z-index: 100;
	position: absolute;
	left: 100px;
	top: 100px;
}
.pophide{
	visibility: hidden;
}

.popWrap > ul{
	margin: 0;
	list-style: none;
	width: 98px;
	height: 98px;
	border: 1px solid #eee;
	background-color: #fff;
	position: relative;
}
.popWrap > ul > li{
	float: left;
	width: 30px;
	height: 30px;
	line-height: 30px;
	text-align: center;
	margin: 2px 0 0 2px;
	background-color: #eee;
	cursor: pointer;
}
.popWrap > ul > li:hover{
	color: #eee;
	background-color: #288bde;
}
.arrow{
	position: absolute;
	left: 44px;
	top: -16px;
	border: none;
}

.goods{
	float: left;
	width: 500px;
	height: 600px;
	border: 2px solid #eee;
	border-radius: 4px;
	margin-top: 10px;
}
.des{
	height: 40px;
	line-height: 40px;
	padding-left: 10px;
	font-size: 16px;
	color: #288bde;
	background-color: #eee;
}
.goods-wrap{
	width: 500px;
	height: 510px;
	overflow: hidden;
}
.g-btn{
	display: inline-block;
	width: 80px;
	padding: 6px 12px;
	margin-bottom: 0;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.428;
	text-align: center;
	color: #999;
	background-color: #eee;
	white-space: nowrap;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	border-radius: 4px;
	border: 1px solid transparent;
}
.g-btn:hover{
	color: #fff;
	background-color: #288bde;
}
.footer-nav{
	height: 40px;
	line-height: 40;
}
.footer-nav ol{
	margin: 0;
	padding: 0;
	list-style: none;
	float: right;
	width: 240px;
	height: 30px;
}
.footer-nav ol li{
	float: left;
	width: 30px;
	height: 30px;
	line-height: 30px;
	margin-left: 5px;
	color: #999;
	background-color: #eee;
	text-align: center;
	cursor: pointer;
}
.footer-nav ol li:hover{
}
.footer-nav ol li:first-child{
	color: #fff;
	background-color: #288bde;
}

.goods-child{
	padding: 10px;
	width: 96%;
	height: 100px;
	border-bottom: 1px solid #ddd;
}
.g-img{
	width: 100px;
	height: 100px;
	float: left;
}
.g-content{
	width: 350px;
	height: 100px;
	float: right;
	margin: 10px;
	position: relative;
	left: 0;
	top: 0;
}
.g-name, .g-detail{
	cursor: pointer;
}
.g-name:hover, .g-detail:hover{
	text-decoration: underline;
	color: orange;
}
.g-detail{
	font-size: 14px;
	color: #288bde;
}
.g-price{
	display: inline-block;
	margin: 0;
	position: absolute;
	right: 30px;
	top: 20px;
	font-size: 20px;
	color: red;
}
